@keyframes floatingone{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 103px); }
	100% { transform: translate(0, -0px); }
}
@keyframes floatingtwo{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 99px); }
	100% { transform: translate(0, -0px); }
}
@keyframes floatingthr{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 102px); }
	100% { transform: translate(0, -0px); }
}
@keyframes floatingfou{
