.landingpageport{
	position: absolute;
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin: 0px 0px 0px 200px;
	width: 400px;
}

.projectpageport{
	position: absolute;
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin: 0px 0px 0px 780px;
	width: 400px;
}

.landingpageport:hover{
	z-index: 100;
	transition: ease 0.3s;
	margin: 0px 0px 0px 150px;
	width: 1100px;
}

.projectpageport:hover{
	z-index: 100;
	transition: ease 0.3s;
	margin: 0px 0px 0px 150px;
	width: 1100px;
}